.Footer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: absolute;
  bottom: 0;
  right: 0;
  color: black;
  padding: 20px;
}

.Footer div {
  background-color: brown;
  color: black;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 10px;
}

.Footer button {
  background-color: blue;
  color: black;
  font-size: 40px;
  font-weight: bolder;
  border-radius: 100%;
  width: 50px;
  height: 50px;
  top: 0;
  right: 0;
}
